import route from "@/common/public.js"
import copyright from "@/components/customize/copyright.vue"
import loginheader from "@/components/customize/loginheader.vue"
export default {
	components: {
	    copyright,
		loginheader
	},
	data() {
		return {
			phoneTailNumber: '',
			phoneNumber:"",
			code:'',
			passwd:"",
			getCodeText:'获取验证码',
			getCodeBtnColor:"#ff0000",
			getCodeisWaiting:false,
			showPassword: true,//显示密码
			Signboard:'',
			Signguid:',',
			telephone:'',//判断输入的手机号和当前账户注册的手机号是否一致
			height:'',
			isLogin:false,
		}
	},
	onLoad() {
		try {
			this.height = this.winHeight
		} catch (e) {
		    // error
		}
		var jsonList = uni.getStorageSync("jsonList");
		if(jsonList){
			var data = JSON.parse(jsonList); //JSON字符串转对象
			this.Signboard = data.Ident_Signboard;
			this.Signguid = data.Ident_Signguid;
			uni.request({
				url:route.variable+'/mobile/Subaccount/getMain', //获取验证时调用的接口
				method:'POST',
				data:{
					Ident_Signboard: this.Signboard,
					Ident_Signguid: this.Signguid,
				},
				success: (result) => {
					console.log(result);
					if(result.data.status==0){
						this.telephone=result.data.telephone;
						this.phoneTailNumber = this.telephone.substring(7, 11)
						this.isLogin = true;
					}else{
						uni.showToast({title: '获取原手机号失败！，'+result.data.message,icon:"none"});
					}
				},
				fail:(e)=>{
					uni.showToast({title: '获取原手机号失败'+'错误码201',icon:"none"});
					console.log("fail: "+'错误码201');
				}
			})
		}else{
			this.isLogin = false;
		}
		
	},
	methods: {
		Timer(){},
		changeStatus(flag){
			switch (flag){
				case 0:
					this.getCodeText = "获取验证码";
					this.getCodeisWaiting = false;
					this.getCodeBtnColor = "#ff0000";
					break;
				case 1:
					this.getCodeText = "发送中...";
					this.getCodeisWaiting = true;
					this.getCodeBtnColor = "rgba(255,0,0,0.5)";
					break;
				case 2:
					this.getCodeText = "重新获取(60)";
					this.getCodeisWaiting = false;
					this.getCodeBtnColor = "#ff0000";
					break;
				default:
					break;
			}
		},
		getCode(){
			if(this.getCodeisWaiting){
				return ;
			}
			if(!(/^1(3|4|5|6|7|8|9)\d{9}$/.test(this.phoneNumber))){ 
				uni.showToast({title: '请填写正确手机号码',icon:"none"});
				return false; 
			} 
			if(this.isLogin == true){
				if(this.telephone!=this.phoneNumber){
					uni.showToast({title: '输入的手机号与该账户注册手机号不一致',icon:"none"});
					return false;
				}
			}
			this.changeStatus(1);
			// this.getCodeText = "发送中..."
			// this.getCodeisWaiting = true;
			// this.getCodeBtnColor = "rgba(255,0,0,0.5)"
			//示例用定时器模拟请求效果
			setTimeout(()=>{
				uni.request({
					url:route.variable+'/mobile/security/verCode', //获取验证时调用的接口
					method:'POST',
					data:{
						telephone:this.phoneNumber
					},
					success: (result) => {
						console.log(result);
						if(result.data.status==0){
							uni.showToast({title: '验证码已发送',icon:"none"});
							
							this.setTimer();
							
						}else{
							uni.showToast({title: '获取验证码失败，'+result.data.message,icon:"none"});
							this.changeStatus(0);
						}
					},
					fail:(e)=>{
						uni.showToast({title: '获取验证码失败'+'错误码201',icon:"none"});
						this.changeStatus(0);
					}
				})
				
			},1000)
			
		},
		changePassword: function() {
			this.showPassword = !this.showPassword;
		},
		setTimer(){
			let holdTime = 60;
			this.changeStatus(2)
			// this.getCodeText = "重新获取(60)"
			this.Timer = setInterval(()=>{
				if(holdTime<=0){
					// this.getCodeisWaiting = false;
					// this.getCodeBtnColor = "#ff0000";
					// this.getCodeText = "获取验证码"
					this.changeStatus(0)
					clearInterval(this.Timer);
					return ;
				}
				this.getCodeText = "重新获取("+holdTime+")"
				holdTime--;
				
			},1000)
		},
		doReset(){
			uni.hideKeyboard()
			if(this.phoneNumber==''){
				uni.showToast({title: '请输入手机号',icon:"none"});
				return false; 
			}
			
			if(!(/^1(3|4|5|6|7|8|9)\d{9}$/.test(this.phoneNumber))){ 
				uni.showToast({title: '请填写正确手机号码',icon:"none"});
				return false; 
			} 

			if(this.code==''){ 
				uni.showToast({title: '请输入验证码',icon:"none"});
				return false; 
			}
			
			if(this.passwd==''){ 
				uni.showToast({title: '请输入新密码',icon:"none"});
				return false; 
			}
			if(!RegExp(/^.{6,}$/).test(this.passwd)){
				uni.showToast({title: '密码格式不对，密码至少6位数',icon:"none"});
				return false; 
			}
			uni.showLoading({
				title: '提交中...'
			})
			uni.request({
				url:route.variable+'/mobile/security/UpPassword',
				method:'POST',
				data:{
					password:this.passwd,
					telephone:this.phoneNumber,
					vercode:this.code
				},
				success: (res) =>{
					console.log(res);
					if(res.data.status==0){
						uni.showToast({
							title:res.data.message,
							icon:'none',
							complete() {
								setTimeout(function() {
									uni.navigateTo({
										url:'../index/login'
									})
								}, 1000);
							}
						})
					}else{
						uni.hideLoading()
						uni.showToast({
							title:res.data.message,
							icon:'none'
						})
					}
				},
				fail:(e)=>{
					uni.hideLoading()
					uni.showToast({title: '修改登陆密码失败'+'错误码201',icon:"none"});
					console.log("fail: "+'错误码201');
				}
			})
		}
	}
}
